<template>
  <b-card>
    <!-- User Interface controls -->
    <b-row>
      <b-col
        sm="5"
        md="6"
        class="my-1"
      >
        <b-form-group
          label="Por página"
          label-for="per-page-select"
          label-cols-sm="6"
          label-cols-md="4"
          label-cols-lg="3"
          label-align-sm="right"
          label-size="sm"
          class="mb-0"
        >
          <b-form-select
            id="per-page-select"
            v-model="perPage"
            :options="pageOptions"
            size="sm"
          />
        </b-form-group>
      </b-col>

      <b-col
        sm="7"
        md="6"
        class="my-1"
      >
        <b-pagination
          v-model="currentPage"
          :total-rows="totalRows"
          :per-page="perPage"
          align="fill"
          size="sm"
          class="my-0"
        />
      </b-col>
    </b-row>

    <!-- Main table element -->
    <b-table
      :items="items"
      :fields="fields"
      :current-page="currentPage"
      :per-page="perPage"
      :filter="filter"
      :filter-included-fields="filterOn"
      :sort-by.sync="sortBy"
      :sort-desc.sync="sortDesc"
      :sort-direction="sortDirection"
      stacked="md"
      show-empty
      class="text-center"
      small
      @filtered="onFiltered"
    >
      <template #cell(denunciante)="row">
        {{ row.value.first }} {{ row.value.last }}
      </template>
      <template #cell(denunciado)="row">
        {{ row.value.first }} {{ row.value.last }}
      </template>
      <template #cell(actions)="row">
        <b-button
          size="sm"
          class="mr-1"
          variant="warning"
          @click="preCalificar"
        >
          Pre calificar
        </b-button>
        <b-button
          size="sm"
          class="mr-1"
          variant="info"
          @click="row.toggleDetails"
        >
          {{ row.detailsShowing ? 'Ocultar' : 'Mostrar' }} resolución
        </b-button>
        <b-button
          size="sm"
          variant="success"
          @click="row.toggleDetails"
        >
          Hacer seguimiento
        </b-button>
      </template>

      <template #row-details>
        <b-card>
          <!-- <ul>
            <li
              v-for="(value, key) in row.item"
              :key="key"
            >
              {{ key }}: {{ value }}
            </li>
          </ul> -->
          <b-button
            v-ripple.400="'rgba(255, 255, 255, 0.15)'"
            variant="danger"
            size="sm"
            pill
          >
            <feather-icon
              icon="UploadIcon"
              class="mr-50"
            />
            <span class="align-middle">Subir Resolución</span>
          </b-button>
        </b-card>
      </template>
    </b-table>

    <!-- Info modal -->
    <b-modal
      :id="infoModal.id"
      :title="infoModal.title"
      ok-only
      size="lg"
      @hide="resetInfoModal"
    >
      <pre>{{ infoModal.content }}</pre>
    </b-modal>
  </b-card>
</template>

<script>
import {
  BCard,
  BPagination,
  BCol,
  BRow,
  BTable,
  BFormGroup,
  // BInputGroup,
  BFormSelect,
  BButton,
  // BInputGroupAppend,
  // BFormCheckboxGroup,
  // BFormCheckbox,
  // BFormInput,
} from 'bootstrap-vue'
import Ripple from 'vue-ripple-directive'

export default {
  components: {
    BCard,
    BPagination,
    BCol,
    BRow,
    BTable,
    BFormGroup,
    // BInputGroup,
    BFormSelect,
    BButton,
    // BInputGroupAppend,
    // BFormCheckboxGroup,
    // BFormCheckbox,
    // BFormInput,
  },
  directives: {
    Ripple,
  },
  data() {
    return {
      items: [
        {
          isActive: true, age: 40, created_at: '09-09-2021', denunciante: { first: 'Dickerson', last: 'Macdonald' }, denunciado: { first: 'Dickerson', last: 'Macdonald' }, resultado: '-',
        },
        {
          isActive: false, age: 21, created_at: '09-09-2021', denunciante: { first: 'Larsen', last: 'Shaw' }, denunciado: { first: 'Dickerson', last: 'Macdonald' }, resultado: 'SI',
        },
        {
          isActive: false,
          age: 9,
          created_at: '09-09-2021',
          denunciante: { first: 'Mini', last: 'Navarro' },
          denunciado: { first: 'Dickerson', last: 'Macdonald' },
          resultado: 'NO',
          // _rowVariant: 'success',
        },
        {
          isActive: false, age: 89, created_at: '09-09-2021', denunciante: { first: 'Geneva', last: 'Wilson' }, denunciado: { first: 'Dickerson', last: 'Macdonald' }, resultado: 'SI',
        },
        {
          isActive: true, age: 38, created_at: '09-09-2021', denunciante: { first: 'Jami', last: 'Carney' }, denunciado: { first: 'Dickerson', last: 'Macdonald' }, resultado: '-',
        },
        {
          isActive: false, age: 27, created_at: '09-09-2021', denunciante: { first: 'Essie', last: 'Dunlap' }, denunciado: { first: 'Dickerson', last: 'Macdonald' }, resultado: 'NO',
        },
        {
          isActive: true, age: 40, created_at: '09-09-2021', denunciante: { first: 'Thor', last: 'Macdonald' }, denunciado: { first: 'Dickerson', last: 'Macdonald' }, resultado: '-',
        },
        {
          isActive: true,
          age: 87,
          created_at: '09-09-2021',
          denunciante: { first: 'Larsen', last: 'Shaw' },
          _cellVariants: { age: 'danger', isActive: 'warning' },
        },
        {
          isActive: false, age: 26, created_at: '09-09-2021', denunciante: { first: 'Mitzi', last: 'Navarro' }, denunciado: { first: 'Dickerson', last: 'Macdonald' },
        },
        {
          isActive: false, age: 22, created_at: '09-09-2021', denunciante: { first: 'Genevieve', last: 'Wilson' }, denunciado: { first: 'Dickerson', last: 'Macdonald' },
        },
        {
          isActive: true, age: 38, created_at: '09-09-2021', denunciante: { first: 'John', last: 'Carney' }, denunciado: { first: 'Dickerson', last: 'Macdonald' },
        },
        {
          isActive: false, age: 29, created_at: '09-09-2021', denunciante: { first: 'Dick', last: 'Dunlap' }, denunciado: { first: 'Dickerson', last: 'Macdonald' },
        },
      ],
      fields: [
        {
          key: 'denunciante', label: 'DENUNCIANTE', sortable: true, sortDirection: 'desc',
        },
        {
          key: 'denunciado', label: 'DENUCIADO', sortable: true, sortDirection: 'desc',
        },
        {
          key: 'created_at', label: 'F. Registro', sortable: true, sortDirection: 'desc',
        },
        {
          key: 'isActive',
          label: 'Estado',
          formatter: value => (value ? 'En proceso' : 'Concluido'),
          sortable: true,
          sortByFormatted: true,
          filterByFormatted: true,
        },
        { key: 'resultado', label: '¿se presentó hostigamiento?' },
        { key: 'actions', label: 'Acciones' },
      ],
      totalRows: 1,
      currentPage: 1,
      perPage: 5,
      pageOptions: [5, 10, 15, { value: 100, text: 'Show a lot' }],
      sortBy: '',
      sortDesc: false,
      sortDirection: 'asc',
      filter: null,
      filterOn: [],
      infoModal: {
        id: 'info-modal',
        title: '',
        content: '',
      },
    }
  },
  computed: {
    sortOptions() {
      // Create an options list from our fields
      return this.fields
        .filter(f => f.sortable)
        .map(f => ({ text: f.label, value: f.key }))
    },
  },
  mounted() {
    // Set the initial number of items
    this.totalRows = this.items.length
  },
  methods: {
    preCalificar() {
      this.$router.push({ name: 'social-denuncias-calificar' })
    },
    info(item, index, button) {
      this.infoModal.title = `Row index: ${index}`
      this.infoModal.content = JSON.stringify(item, null, 2)
      this.$root.$emit('bv::show::modal', this.infoModal.id, button)
    },
    resetInfoModal() {
      this.infoModal.title = ''
      this.infoModal.content = ''
    },
    onFiltered(filteredItems) {
      // Trigger pagination to update the number of buttons/pages due to filtering
      this.totalRows = filteredItems.length
      this.currentPage = 1
    },
  },
}
</script>
